import React from 'react';

import './home.css';
import background from '../../images/background_3.jpg';

const header = () => {
    return (
        <header style={{ backgroundImage: `url(${background})` }}>
            <div className="name_div">
                <h2 className="name_div_head">Annukul</h2>
            </div>

            <div className="about_div">
                <h2>I am a <br /><strong>Full Stack Developer.</strong></h2>
                <div className="downArrow bounce">
                    <a href="#content"><img width="40" height="40" alt="" src="data:image/svg+xml;base64," /></a>
                </div>
            </div>

            <div className="options_div">
                <ul>
                    <li><a href="#content">Tech</a></li>
                    <li><a href="#about">About</a></li>
                    <li><a href="#projects">Projects</a></li>
                    <li><a href="#contact">Contact</a></li>
                </ul>
            </div>
        </header>
    )
}

export default header;
